<template>
    <div class="activity-tab">
              <div id="accordion" class="w-100">
                <div class="card" v-for="(item, parentIndex) in TimeLineData" :key="parentIndex">
                    <div class="card-header">
                        <a class="card-link collapsed" data-toggle="collapse" v-bind:href="'#collapseOne'+parentIndex">
                            <div class="conversation-date heading-border"><span><em class="fa fa-angle-down rotate-icon pr-1" aria-hidden="true"></em>{{ item.groupKey}}</span></div>
                        </a>
                    </div>
                    <div v-bind:id="'collapseOne'+parentIndex" v-bind:class="{'collapse':true, 'show':parentIndex==0}">
                        <div class="card-body">
                            <div class="row" style="max-height:400px; overflow-y:auto;">                            
        <div class="col-md-12 mbspc" v-for="(childItem, childIndex) in item.groupData" :key="childIndex">
        <div class="row">
        <div class="col-xl-5 col-lg-5 col-md-12 col-sm-12 message-ellapse" v-bind:title="childItem.message + ' '+ $t('By') +' ' + childItem.created_by_name">
        <span class="iconplus"><em class="fa fa-check-circle"></em></span><span class="circle-oicon"><em v-bind:class="'fa '+childItem.icon_codes"></em></span> {{childItem.message}}  {{$t('By') }} {{childItem.created_by_name}}
        </div>
        <div class="col-xl-4  col-lg-4 col-md-12 col-sm-12">
        <div class="cont_start"><em class="fa fa-envelope-o"></em><span class="blue-time">{{childItem.created_by_name}}</span> ({{childItem.mothyear}})</div>
        </div>
        <div class="col-xl-3  col-lg-3 col-md-12 col-sm-12 text-right">
        <p>{{childItem.Complete_datetime | formatTime}}</p>
        </div>
        </div>
        </div>                          
                            </div>
                        </div>
                    </div>
        </div>
       
        <div v-if="NoRecordForTab" class="text-center text-danger p-2">  {{$t('NoRecordfound')}} </div>
        </div>
            </div>
</template>
<script>
export default {
        props: {
        timelinedata: {
            type: Array,
            required: true
        }
    },
    data: function () {
        return {
            TimeLineData: [],
            NoRecordForTab: true
        }
    },
    watch: {
      
        timelinedata:{
            handler(value) {                
                this.TimeLineData = value;
                this.NoRecordForTab = ((this.TimeLineData != null && this.TimeLineData.length > 0) ? false : true);
            },
            deep: true
        }
    },
    methods: {

    },
}
</script>